/* App.css */

.size {
  max-width: 100%;
  height: auto;
  width: 300px; /* Set a default width */
}

.moving-button {
  position: absolute;
  z-index: 10;
  transition: top 0.3s ease, left 0.3s ease;
}


/* Ensure buttons don't overflow or get cut off on small screens */
@media (max-width: 768px) {
  .size {
    width: 200px;
  }

  .moving-button {
    padding: 0.5rem 1rem;
    font-size: 0.875rem;
  }

  .App {
    padding: 1rem;
  }
}
